export default {
    data() {
        return {
            blog: {},
            thumbnails: []
        };
    },
    onLoad(options) {
        let blogID = options.blogID;
        this.getBlog(blogID);
    },
    computed: {
        htmlData() {
            let html = this.blog.html;
            html && this.thumbnails.length > 0
                ? this.thumbnails.forEach(v => {
                      let reg = new RegExp(v._id, "ig");
                      html = html.replace(reg, e => {
                          return v.data;
                      });
                  })
                : "";
            return html;
        }
    },
    methods: {
        async getBlog(id) {
            let data = await this.$http(
                "/blog-service/get-index-blog?id=" + id,
                "GET"
            );
            this.blog = data;

            if (data.thumbnails.length) {
                let thumbnails = await this.getThumbnails(data._id);

                this.thumbnails = thumbnails;
            }
        },
        async getThumbnails(id) {
            return await this.$http("/blog-service/thumbnails/" + id, "GET");
        },
        bufferToURL(buf) {
            function toArrayBuffer(buf) {
                var ab = new ArrayBuffer(buf.length);
                var view = new Uint8Array(ab);
                for (var i = 0; i < buf.length; ++i) {
                    view[i] = buf[i];
                }
                return ab;
            }
            let blob = new Blob([toArrayBuffer(buf.data)], {
                type: "image/*"
            });
            return URL.createObjectURL(blob);
        },
        BufferToBase64(buffer) {
            let bytes = new Uint8Array(buffer.data);
            let data = "";
            let len = bytes.byteLength;
            for (let i = 0; i < len; i++) {
                data += String.fromCharCode(bytes[i]);
            }
            return "data:image/png;base64," + window.btoa(data);
        }
    }
};
